<template>
  <section class="section">
    <h2 class="title is-flex is-align-items-center">
      <span class="icon has-text-danger mr-4">
        <i class="fas fa-map-marked"></i>
      </span>
      {{ $t("place.pagename") }}
    </h2>

    <div class="box">
      <div class="content">
        <h4 class="is-uppercase">{{ $t("place.subheader") }}</h4>
        <p>
          {{ $t("place.description") }}
        </p>
        <div class="buttons">
          <button class="button" @click="modalEditToggle">
            {{ place.id ? $t("place.btnEdit") : $t("place.btnAdd") }}
          </button>
          <button v-if="place.id" class="button" @click="modalQRToggle">
            {{ $t("place.btnShowQr") }}
          </button>
        </div>
      </div>
    </div>

    <div class="box" v-if="!placeClaim">
      <h4 class="subtitle is-4 has-text-danger">
        {{ $t("place.emptyMessage") }}
      </h4>
    </div>

    <div class="box" v-else>
      <div class="columns">
        <div class="column">
          <div class="columns is-multiline">
            <div class="column is-one-third">
              <div class="content">
                <p>{{ $t("place.labels.link") }}</p>
                <h4>{{ place.id }}</h4>
              </div>
            </div>

            <div class="column is-one-third">
              <div class="content">
                <p>{{ $t("place.labels.name") }}</p>
                <h4>{{ place.name }}</h4>
              </div>
            </div>

            <div class="column is-one-third">
              <div class="content">
                <p>{{ $t("place.labels.tables") }}</p>
                <h4>{{ place.tables }}</h4>
              </div>
            </div>

            <div class="column is-one-third">
              <div class="content">
                <p>{{ $t("place.labels.address") }}</p>
                <h4>{{ place.address }}</h4>
              </div>
            </div>

            <div class="column is-one-third">
              <div class="content">
                <p>{{ $t("place.labels.city") }}</p>
                <h4>{{ place.city }}</h4>
              </div>
            </div>

            <div class="column is-one-third">
              <div class="content">
                <p>{{ $t("place.labels.country") }}</p>
                <h4>{{ place.country }}</h4>
              </div>
            </div>

            <div class="column is-one-third">
              <div class="content">
                <p>{{ $t("place.labels.phone") }}</p>
                <h4>{{ place.phone }}</h4>
              </div>
            </div>

            <div class="column is-one-third">
              <div class="content">
                <p>{{ $t("place.labels.email") }}</p>
                <h4>{{ place.email }}</h4>
              </div>
            </div>

            <div class="column is-full">
              <div class="content">
                <p>{{ $t("place.labels.description") }}</p>
                <h4>{{ place.description }}</h4>
              </div>
            </div>
          </div>
        </div>
        <div class="column is-narrow">
          <div class="content">
            <h5 class="">{{ $t("place.labels.uploadBg") }}</h5>

            <div class="block">
              <!-- <figure class="image is-128x128"> -->
              <figure class="image is-5by3 mx-0 has-background-grey-lighter">
                <img :src="place.backgroundImage" />
              </figure>
            </div>

            <div class="block">
              <div class="file">
                <label class="file-label">
                  <input
                    class="file-input"
                    type="file"
                    name="resume"
                    @click="resetFileImage('backgroundImage')"
                    @change="uploadFileImage('backgroundImage', $event)"
                  />
                  <span class="file-cta">
                    <span class="file-icon">
                      <i class="fas fa-upload"></i>
                    </span>
                    <span class="file-label">
                      {{ $t("general.btnUpload") }}
                    </span>
                  </span>
                </label>
              </div>
            </div>
          </div>
          <div class="content">
            <h5 class="">{{ $t("place.labels.uploadLogo") }}</h5>

            <div class="block">
              <!-- <figure class="image is-128x128"> -->
              <figure class="image is-3by1 mx-0 has-background-grey-lighter">
                <img :src="place.logoImage" />
              </figure>
            </div>

            <div class="block">
              <div class="file">
                <label class="file-label">
                  <input
                    class="file-input"
                    type="file"
                    name="resume"
                    @click="resetFileImage('logoImage')"
                    @change="uploadFileImage('logoImage', $event)"
                  />
                  <span class="file-cta">
                    <span class="file-icon">
                      <i class="fas fa-upload"></i>
                    </span>
                    <span class="file-label">
                      {{ $t("general.btnUpload") }}
                    </span>
                  </span>
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- ================= MODAL PART ================= -->

    <PlaceModalQR v-if="modalQR" @modalClose="modalQRToggle" />
    <PlaceModalEdit
      v-if="modalEdit"
      :initialPlace="place"
      @modalClose="modalEditToggle"
    />
  </section>
</template>

<script>
import PlaceModalQR from "@/components/PlaceModalQR";
import PlaceModalEdit from "@/components/PlaceModalEdit";
export default {
  name: "Place",
  components: { PlaceModalQR, PlaceModalEdit },
  data() {
    return {
      qrCodes: {},
      qrCode: null,
      modalQR: false,
      modalEdit: false,
      backgroundImage: {
        name: null,
        image: null,
      },
      logoImage: {
        name: null,
        image: null,
      },
    };
  },

  computed: {
    place() {
      return (
        this.$store.state.place || {
          id: "",
          name: "",
          description: "",
          phone: "",
          email: "",
          address: "",
          city: "",
          country: "",
          tables: "",
        }
      );
    },
    placeClaim() {
      return this.$store.state.claims?.place;
    },
  },

  methods: {
    modalEditToggle() {
      this.modalEdit = !this.modalEdit;
    },

    modalQRToggle() {
      this.modalQR = !this.modalQR;
    },

    // https://codepen.io/Atinux/pen/qOvawK/
    async uploadFileImage(image, event) {
      this[image].name = event.target.files[0];
      var reader = new FileReader();
      reader.onload = async (e) => {
        this[image].image = e.target.result;
      };
      reader.readAsDataURL(this[image].name);
      await this.$store.dispatch("savePlaceImage", {
        name: image,
        data: this[image].name,
      });
    },
    resetFileImage(image) {
      this.$refs[image] = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.textarea {
  resize: none;
}
</style>